@font-face {
  font-family: Inter;
  src: local(Inter Regular), local(Inter-Regular), url("inter-regular.b0ee6dc1.woff2") format("woff2"), url("inter-regular.9d1ba490.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: local(Inter Medium), local(Inter-Medium), url("inter-medium.67bf75ff.woff2") format("woff2"), url("inter-medium.ffd6703b.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: local(Inter SemiBold), local(Inter-SemiBold), url("inter-semibold.fad4f7d9.woff2") format("woff2"), url("inter-semibold.ef36d546.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--color-page-bcg);
  font-family: Inter;
}

.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1145px;
  }
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.off-scroll {
  overflow-y: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.btn {
  min-width: 128px;
  min-height: 32px;
  color: var(--color-button-text);
  border: 1px solid var(--color-button-card-bcg);
  background-color: var(--color-button-bcg);
  border-radius: 15px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.btn:hover, .btn:focus {
  color: var(--color-hover-text);
  background-color: var(--color-hover-bcg);
}

:root {
  --color-page-bcg: #f8f8f8;
  --color-element-bcg: #050505;
  --color-menu-mobile-bcg: #9bb537;
  --color-title-text: #050505;
  --color-green-title-text: #9bb537;
  --color-poprecipe-text: rgba(5, 5, 5, .8);
  --color-card-hero-text: rgba(248, 248, 248, .6);
  --color-card-text: #f8f8f8;
  --color-selector-text: rgba(5, 5, 5, .5);
  --color-selector-categ-text: rgba(5, 5, 5, .3);
  --color-selector-categ: rgba(5, 5, 5, .2);
  --color-switch-bcg: #cdcdcd;
  --color-button-text: #050505;
  --color-button-bcg: #f8f8f8;
  --color-button-card-bcg: #9bb537;
  --color-rating-bcg: #eea10c;
  --color-hover-bcg: #9bb537;
  --color-hover-text: #050505;
  --color-selector-check: #050505;
  --color-select-open-bcg: #fff;
  --color-modal-recipe-placeholder: rgba(5, 5, 5, .1);
  --color-modal-recipe-text: rgba(5, 5, 5, .8);
  --color-modal-recipe-rating-text: rgba(5, 5, 5, .5);
  --color-modal-recipe-tags-border: rgba(5, 5, 5, .2);
  --color-modal-recipe-backdrop: rgba(5, 5, 5, .3);
  --color-modal-recipe-backdground: #fff;
  --transition-dur-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --reviews-card-shadow: 0px 9.04932px 26.5717px rgba(215, 228, 249, .25);
}

[dark] {
  --color-page-bcg: #161616;
  --color-title-text: #050505;
  --color-element-bcg: #fff;
  --color-title-text: #fff;
  --color-button-bcg: #1e1e1e;
  --color-button-text: #fff;
  --color-selector-text: rgba(255, 255, 255, .5);
  --color-selector-check: #fff;
  --color-select-open-bcg: #050505;
  --color-selector-categ-text: rgba(255, 255, 255, .3);
  --color-selector-categ: rgba(255, 255, 255, .2);
  --color-poprecipe-text: rgba(255, 255, 255, .8);
}

.hero-slider {
  width: 100%;
  height: 100vh;
  text-align: center;
  background-image: url("https://i.etsystatic.com/10606713/r/il/bb16fa/3099215194/il_1588xN.3099215194_71c8.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  animation: change 20s ease-in-out infinite;
  position: relative;
  overflow: hidden;
}

.slider-content {
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.logo-text {
  flex-direction: column;
  padding-bottom: 20px;
  font-size: 70px;
  display: flex;
}

.slider-text {
  color: #0056b9;
  background: #ffd800;
  border-radius: 20px;
  padding: 8px 24px;
  font-size: 18px;
  text-decoration: none;
}

.slider-text:hover {
  color: #ffd800;
  background: #0056b9;
}

@keyframes change {
  0% {
    background-image: url("https://i.etsystatic.com/9145805/r/il/f7b2b6/3651458542/il_1588xN.3651458542_34p7.jpg");
  }

  20% {
    background-image: url("https://i.etsystatic.com/10210799/r/il/ee31fb/2970393336/il_1588xN.2970393336_64wl.jpg");
  }

  40% {
    background-image: url("https://i.etsystatic.com/20638585/r/il/861787/5112883908/il_1588xN.5112883908_7ewt.jpg");
  }

  60% {
    background-image: url("https://i.etsystatic.com/10606713/r/il/747710/5189298007/il_1588xN.5189298007_d21u.jpg");
  }

  80% {
    background-image: url("https://i.etsystatic.com/10606713/r/il/2579f9/5189297967/il_1588xN.5189297967_6tmc.jpg");
  }

  100% {
    background-image: url("https://i.etsystatic.com/9145805/r/il/3e10ce/3757167861/il_1588xN.3757167861_b4b6.jpg");
  }
}

